import React, { Component } from 'react';
import PropTypes from 'prop-types';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Tooltip from '@material-ui/core/Tooltip';

import logo from '../../images/logo.svg';
import carrito from '../../images/carrito.svg';

import Button from '@material-ui/core/Button';
import Hidden from '@material-ui/core/Hidden';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';

import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({        
    root: {
        zIndex:500,
    },
    toolbar: {
        justifyContent:'space-between',
    },
    columna: {
        width: 60,
    },
    logo: {
        height: 41, width: 41,
    },
    carrito: {
        height: 36,
    },
    badge: {
        top: 8,        
        padding: '10px 6px',                 
        border: `2px solid ${theme.palette.primary.dark}`,
        fontSize: 16,
        background: '#fff',
        color: theme.palette.primary.dark,
    },

    dialogContent: {        
        minHeight: '35vh', 
        display:'flex', 
        alignItems:'center'
    },
    dialogContentText: {
        fontFamily: `Velvet, 'sans-serif'`,
        fontWeight: 600,
        fontSize: 22,
        [theme.breakpoints.up('sm')]: {
            fontSize: 30,
        },
    },
    dialogCta: {
        fontFamily: `Velvet, 'sans-serif'`,
        fontWeight: 600,
        fontSize: 16,
        padding: 10,
        [theme.breakpoints.up('sm')]: {
            fontSize: 22,
        },
    }
});

class Menu extends Component {

    state = {
        open: false,
    };
  
    handleClickOpen = () => {
        this.setState({ open: true });
    };
  
    handleClose = () => {
        this.setState({ open: false });
    };

    render() {
        const { classes, unidades, fixed, homeSeleccionable, carritoVisible, carritoSeleccionable, exitConfirmation,
            handleHomeClick, handleCarritoClick } = this.props;
        return (
            <React.Fragment>
                <AppBar position={ fixed ? 'fixed' : 'static' } className={classes.root}>
                    <Toolbar className={classes.toolbar}>                
                        <div className={classes.columna}>
                            
                        </div>
                        <Tooltip title='Volver a Home'>
                            <IconButton       
                                disabled={ homeSeleccionable ? false : true }
                                onClick={ exitConfirmation && (unidades > 0) ? this.handleClickOpen : handleHomeClick }                                    
                                color='inherit' aria-label="Men de opciones"
                            >
                                <img className={classes.logo} src={logo} alt='Velvet chocolates' />
                            </IconButton>                
                        </Tooltip>
                        
                        <div className={classes.columna}>
                            { 
                                carritoVisible &&
                                    <Tooltip title='Ver el carrito y avanzar con la compra'>
                                        <IconButton                     
                                            disabled= { carritoSeleccionable ? false : true }
                                            onClick={handleCarritoClick}
                                            color='inherit' aria-label="Carrito de compras"                                
                                        >
                                            <Badge 
                                                invisible={ unidades < 1 } 
                                                badgeContent={<b>{unidades}</b>} classes={{ badge: classes.badge}}
                                            >
                                                <img className={classes.carrito} src={carrito} alt='Carrito de compras' />
                                            </Badge>                        
                                        </IconButton>
                                    </Tooltip>
                            }
                        </div>
                    </Toolbar>
                </AppBar>


                <Dialog
                    open={this.state.open}
                    onClose={this.handleClose}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"                                
                >                
                    <DialogContent className={classes.dialogContent}>
                        <DialogContentText 
                            id="alert-dialog-description" 
                            className={classes.dialogContentText}    
                                                    
                        >                        
                            Seguro que queres volver a la pagina principal y abandonar tu compra?
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions className={classes.dialogActions}>
                        <Button onClick={handleHomeClick} size='small' >
                            <Hidden smUp>
                                Volver 
                                <br />
                                atrs
                            </Hidden>
                            <Hidden only='xs'>
                                Volver atrs
                            </Hidden>                            
                        </Button>
                        <Button onClick={this.handleClose} variant='contained' color='primary' size='small' autoFocus className={classes.dialogCta}>                          
                            Continuar compra                            
                        </Button>
                    </DialogActions>
                </Dialog>
            </React.Fragment>            
        );
    }
}

Menu.propTypes = {
    classes: PropTypes.object.isRequired,    
    unidades: PropTypes.number.isRequired,
    fixed: PropTypes.bool.isRequired,
    homeSeleccionable: PropTypes.bool.isRequired,
    exitConfirmation: PropTypes.bool.isRequired,
    carritoVisible: PropTypes.bool.isRequired,
    carritoSeleccionable: PropTypes.bool.isRequired,
    handleHomeClick: PropTypes.func,
    handleCarritoClick: PropTypes.func,
};

export default withStyles(styles)(Menu);