import React from 'react';
import PropTypes from 'prop-types';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';

import { Field } from 'formik';
import { TextField, RadioGroup } from 'formik-material-ui';

import { isRequired, validarPaso2 } from '../../helpers/checkoutHelpers';

import Flash from 'react-reveal/Flash';

import withStyles from '@material-ui/core/styles/withStyles';
import { Tooltip } from '@material-ui/core';
const styles = theme => ({    
    buttons: {
        display: 'flex',
        margin: '0 auto',
        marginTop: theme.spacing.unit * 3,        
        marginBottom: theme.spacing.unit * 2,
        justifyContent: 'center',      
    },
    button: {                
        marginRight: theme.spacing.unit,
        paddingLeft: theme.spacing.unit * 4,
        paddingRight: theme.spacing.unit * 4,
        paddingTop: theme.spacing.unit * 1.5,
        paddingBottom: theme.spacing.unit * 1.5,
        fontSize: 18
    },
    cta: {
        fontFamily:`Velvet, 'sans-serif'`,
        fontWeight: 600,
        textShadow: '2px 2px 2px #444',
        fontSize: 24,
        letterSpacing: 1.4,
        paddingLeft: theme.spacing.unit * 4,
        paddingRight: theme.spacing.unit * 4,
        paddingTop: 10,
        paddingBottom: 4,       
    },

    botonOpcion: {
        textTransform:'none',     
        margin: 0,     
        padding:`${theme.spacing.unit * 1.5}px ${theme.spacing.unit}px`
    },
    
    labelRetiro: {
        fontSize: 16,
        marginTop: theme.spacing.unit * 3,
        textAlign: 'justify',
    }
});

const FormEntrega = ({ classes, dia1, dia2, direcciones, validationStep, values, errors, 
handleNext, handleNextValidationStep, setFieldTouched, setFieldValue, handleBack, handleRemoverDireccion }) => {

    return (
        <Grid container >                                                                                                        
                                                
            <Grid item xs={12}  >
                {
                    (validationStep === 2) && errors.tipoEntrega ?
                        <Flash>
                            <Typography variant="subtitle2" align='center' style={{fontSize: 20, color: 'red'}}>
                                Eleg una forma de entrega
                            </Typography>
                        </Flash>
                    :
                        <Typography variant="subtitle2" align='center' style={{fontSize: 20}}>
                            Eleg una forma de entrega
                        </Typography>
                }
            </Grid>
            
            <Grid item xs={12}>

                <Field name="tipoEntrega" validate={isRequired} component={RadioGroup}  >                                                                                                                                    
                    
                    <FormControlLabel 
                        value="retiro" control={<Radio color='primary'/>} 
                        label={
                            <span>Retiro por el barrio de <b>Colegiales (Santos Dumont y Cramer)</b>, a coordinar da y horario con el vendedor.</span>                            
                            } 
                        classes={{ label: classes.labelRetiro}}
                    />
                    
                    <FormControlLabel 
                        value="envio" control={<Radio color='primary'/>} 
                        label={
                            <span>Envo a domicilio (nicamente en barrios <b>Colegiales</b> - <b>Belgrano</b> -  <b>Palermo</b> - <b>Nez</b> -  <b>Caitas</b> - <b>Coghlan</b> y <b>Villa Urquiza</b>)</span>
                        }
                        classes={{ label: classes.labelRetiro}}
                    />

                    
                </Field>
                <Typography variant="subtitle2" style={{marginTop: 8, marginLeft: 32}}>
                    Costo de envo $100
                </Typography>
            </Grid>

            
            { values.tipoEntrega === 'envio' && 

                <div 
                    style={{width: '95%', margin: '0 auto', marginTop: 8,
                    border: '1px solid #eee', borderRadius: 4, paddingBottom: 12, textAlign:'center'}}
                >
                    
                    {
                    (validationStep === 2) && errors.entregaDia ?
                        <Flash>
                            <Typography variant="subtitle2" align='center' style={{fontSize: 20, marginTop: 16, color: 'red'}}>
                                Eleg un da de entrega
                            </Typography>
                        </Flash>
                    :
                        <Typography variant="subtitle2" align='center' style={{fontSize: 20, marginTop: 16}}>
                            Eleg un da de entrega
                        </Typography>
                    }   
                    <Field 
                        validate={isRequired} name="entregaDia" component={RadioGroup} 
                        style={{width: '100%',flexDirection: 'row',justifyContent: 'center'}}
                    >
                        <FormControlLabel
                            value={dia1}
                            control=
                            {
                                <Radio                                                                                    
                                    icon={ 
                                        <Button 
                                            className={classes.botonOpcion}
                                            style={{                                                                                        
                                                color: (validationStep === 2) && errors.entregaDia ? 'red' :'',
                                                borderColor: (validationStep === 2) && errors.entregaDia ? 'red' :''
                                            }} 
                                            variant="outlined"
                                        >
                                            {dia1}
                                        </Button>                                                                                
                                    }
                                    checkedIcon={                                                                                
                                        <Button className={classes.botonOpcion} variant="contained" color='primary'>{dia1}</Button>                                                                                
                                    }                                                                            
                                />
                            }
                        />
                        <FormControlLabel
                            value={dia2}
                            control=
                            {
                                <Radio                                                                                    
                                    icon={ 
                                        <Button 
                                            className={classes.botonOpcion}
                                            style={{                                                                                        
                                                color: (validationStep === 2) && errors.entregaDia ? 'red' :'',
                                                borderColor: (validationStep === 2) && errors.entregaDia ? 'red' :''
                                            }}
                                            variant="outlined">
                                                {dia2}
                                            </Button> 
                                    }
                                    checkedIcon={                                                                                
                                        <Button className={classes.botonOpcion} variant="contained" color='primary'>{dia2}</Button>                                                                                
                                    }                                                                            
                                />
                            }
                        />                                                                        
                    </Field>

                    {
                    (validationStep === 2) && errors.entregaDireccion ?
                        <Flash>
                            <Typography variant="subtitle2" align='center' style={{fontSize: 20, marginTop: 16, marginBottom: 4, color: 'red'}}>
                                Y una direccin de entrega
                            </Typography>
                        </Flash>
                    :
                        <Typography variant="subtitle2" align='center' style={{fontSize: 20, marginTop: 16, marginBottom: 4}}>
                            Y una direccin de entrega
                        </Typography>
                    }                       

                    { direcciones.length > 0 &&                                                        
                        <Grid item container xs={12} style={{marginTop: 8}}>
                            <Grid item xs={12} sm={4}>
                                <Typography variant="caption" align='center'>
                                    Direcciones utilizadas anteriormente:
                                </Typography>
                            </Grid>
                            <Grid item container xs={12} sm={8}>
                                {
                                    direcciones.map((direccion, index) => (
                                        <Grid item key={index}>                                                                            
                                            <Chip                                                                                                                        
                                                label={direccion}
                                                onClick={() => setFieldValue('entregaDireccion', direccion)}
                                                onDelete={() => handleRemoverDireccion(direccion)} 
                                                style={{margin: 4, marginTop: 0, marginBottom: 8}}  
                                                color={direccion === values.entregaDireccion ? 'primary' : 'default'}                                                 
                                            />
                                        </Grid>
                                    ))
                                }
                            </Grid>

                        </Grid>                                                        
                    }
                    
                    <Grid item xs={10} style={{margin: '0 auto'}}>
                        <Field 
                            onKeyPress={ 
                                (e) => {
                                    if (e.key === 'Enter') {                                                                            
                                        validarPaso2(values, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                                    }
                                }
                            }
                            variant='outlined' fullWidth type="text" component={TextField} name="entregaDireccion" 
                            validate={isRequired} label="Direccin de entrega"
                        />
                    </Grid>
                </div>

                
            }
            

            { validationStep === 2 && errors.tipoEntrega && 
                
                    <Typography variant="subtitle2" align='center' style={{width: '100%', fontSize: 16, color: 'red', marginTop: 12, marginBottom: -16}}>
                        Eleg una forma de entrega para continuar
                    </Typography>
                
            }

            <div className={classes.buttons} style={{marginTop: 24}}>                                                                                        
                <Tooltip title='Volver a Tus datos'>
                    <Button size='small' onClick={() => handleBack()} className={classes.button} style={{fontSize: 12}}>
                        Atrs
                    </Button>                           
                </Tooltip>                     
                <Tooltip title='Ir a revisar tu pedido para finalizar'>
                    <Button
                        size='large'
                        variant="contained"
                        color="primary"
                        onClick={() => validarPaso2(values, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)}                                            
                        className={classes.cta}
                    >
                        Continuar
                    </Button>
                </Tooltip>
            </div>                                        
        </Grid>
    );
};

FormEntrega.propTypes = {
    classes: PropTypes.object.isRequired,
    dia1: PropTypes.string.isRequired,
    dia2: PropTypes.string.isRequired,
    direcciones: PropTypes.array.isRequired,
    validationStep: PropTypes.number.isRequired,
    values: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired,
    handleNext: PropTypes.func.isRequired, 
    handleNextValidationStep: PropTypes.func.isRequired, 
    setFieldTouched: PropTypes.func.isRequired, 
    setFieldValue: PropTypes.func.isRequired, 
    handleBack: PropTypes.func.isRequired, 
    handleRemoverDireccion: PropTypes.func.isRequired,
};

export default withStyles(styles)(FormEntrega);