import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Dialog from '@material-ui/core/Dialog';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Divider from '@material-ui/core/Divider';
import Tooltip from '@material-ui/core/Tooltip';

import { Formik, Field, Form } from 'formik';
import { TextField } from 'formik-material-ui';

import Zoom from 'react-reveal/Zoom';
import Slide from 'react-reveal/Slide';
import Fade from 'react-reveal/Fade';

import BackIcon from '@material-ui/icons/ArrowBackRounded';

import CheckIcon from '@material-ui/icons/CheckRounded';
import Link from 'react-router-dom/Link';


const styles = theme => ({  
  dialog: {    
    maxWidth: 800,    
    margin: '0 auto',    
    zIndex: 999,
  },
  titulo: {
    marginTop: 0,
    paddingRight: theme.spacing.unit * 2,
    paddingLeft: theme.spacing.unit * 2,
    fontFamily: `Velvet, 'sans-serif'`,
    fontWeight: 600,
    fontSize: 32,
    [theme.breakpoints.up('sm')]: {
        marginTop: theme.spacing.unit * 3,
        fontSize: 36,
    },
  },
  linea: {
    marginTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit * 2,
    paddingLeft: theme.spacing.unit * 2,
    fontFamily: `Velvet, 'sans-serif'`,
    fontWeight: 600,
    fontSize: 22,
    [theme.breakpoints.up('sm')]: {        
        fontSize: 26,
    },
  },
  volverHeader: {
    margin: 0, 
    padding: theme.spacing.unit * 0.5,
  },
  volverTexto: {
    color:'#442c2e',
    fontSize: 12,    
  },
  volverIcono: {
    color:'#442c2e',
    fontSize: 12,
    marginRight: theme.spacing.unit,
  },  
  
  checkIcon: {
    fontSize: 120, 
    color: 'green', 
    width: '100%', 
    marginBottom: -25,
  },
  divider1: {
    marginTop: theme.spacing.unit * 2, marginBottom: theme.spacing.unit * 4
  },
  form: {
    width: '90%', 
    margin: '0 auto', 
    textAlign:'center', 
    marginTop: theme.spacing.unit * 2
  },
  submit: {   
    marginTop: theme.spacing.unit,
    fontFamily:`Velvet, 'sans-serif'`,
    fontWeight: 600,
    textShadow: '2px 2px 2px #444',
    fontSize: 20,
    letterSpacing: 1.6,
    paddingLeft: theme.spacing.unit * 2,
    paddingRight: theme.spacing.unit * 2,
    paddingTop: 6,
    paddingBottom: 2,
  },
  goBack: {        
    margin: '0 auto',
    marginTop: theme.spacing.unit * 3,
    fontFamily:`Velvet, 'sans-serif'`,
    fontWeight: 600, 
    fontSize: 16,
    letterSpacing: 1.4,
    paddingLeft: theme.spacing.unit * 2,
    paddingRight: theme.spacing.unit * 2,
    paddingTop: 6,
    paddingBottom: 2,
  }
});


const GraciasLayout = ({ classes, pedidoUsuario, addComentario, handleBackClick }) => {
  return (
    <Dialog
        open
        onClose={handleBackClick}
        scroll='paper'
        fullScreen
        aria-labelledby="aria-title"
        className={classes.dialog}        
      >          
        
        <DialogTitle disableTypography className={classes.volverHeader}>
          <Tooltip title='Volver a Home'>
            <Link to='/' style={{textDecoration: 'none'}}>
              <Button color="secondary" className={classes.volverTexto}>
                <BackIcon fontSize="small" className={classes.volverIcono} />
                  Volver a pgina principal
              </Button>
            </Link>
          </Tooltip>
        </DialogTitle>
        
        <DialogContent style={{textAlign:'center'}}>
                        
              <Zoom>
                <CheckIcon className={classes.checkIcon} />
              </Zoom>
              <Slide bottom>
                <Typography variant="h2" align='center' gutterBottom className={classes.titulo} >
                  Tu pedido fue realizado correctamente
                </Typography>
              </Slide>                         

              <Divider variant="middle" className={classes.divider1}/>
              
              <Fade bottom>  
                <Typography variant="h2" align='center' gutterBottom className={classes.linea} >
                  Gracias por tu compra
                </Typography>          
                <Typography variant="h2" align='center' gutterBottom className={classes.linea} >
                  Si queres dejanos tu comentario:
                </Typography> 

                <Formik
                  initialValues={{ comentarios: ''}}                
                  onSubmit={(values, { setSubmitting }) => {
                      if (values.comentarios !== '') {
                        addComentario(pedidoUsuario.id, values.comentarios);
                      }                      
                      setSubmitting(false);                     
                      handleBackClick();                           
                  }}
                >
                  {({                                                
                      handleSubmit,
                      isSubmitting,                                            
                  }) => (
                      <Form className={classes.form}>                           
                        
                        <Field 
                            onKeyPress={ (e) => { if (e.key === 'Enter') {handleSubmit()} }}
                            fullWidth type="text" multiline rows='4' variant='outlined'
                            component={TextField} name="comentarios" label="Comentarios"                                  
                        />                  
                        <Button 
                          type='submit' disabled={isSubmitting}                                                        
                          color='primary' size='small' variant='contained' className={classes.submit}                                                    
                        >
                            OK
                        </Button>                          
                      </Form>
                  )}
                </Formik>                
              </Fade>

              <Tooltip title='Volver a Home'>
                <Link to='/' style={{textDecoration: 'none'}}>
                  <Button size='small' className={classes.goBack}>
                    Volver a la pagina principal
                  </Button>
                </Link>
              </Tooltip>

        </DialogContent>      
      
      </Dialog>    
  );
};


GraciasLayout.propTypes = {
  classes: PropTypes.object.isRequired,  
  pedidoUsuario: PropTypes.object.isRequired,
  addComentario: PropTypes.func.isRequired,
  handleBackClick: PropTypes.func.isRequired,
};

export default withStyles(styles)(GraciasLayout);