import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import LinearProgress from '@material-ui/core/LinearProgress';
import PhoneIcon from '@material-ui/icons/PhoneIphoneRounded';
import MailIcon from '@material-ui/icons/MailOutlineRounded';
import { Tooltip } from '@material-ui/core';

const styles = theme => ({
  listItem: {
    padding: `${theme.spacing.unit}px 0`,
  },
  total: {
    fontWeight: '700',
  },
  title: {
    marginTop: theme.spacing.unit * 2,
  },  
  imagen: {
    width: 48, 
    height: 48,
    boxShadow: theme.shadows[1],
    marginRight: theme.spacing.unit,
    [theme.breakpoints.up('sm')]: {
      width: 60, 
      height: 60,      
    },    
  },

  buttons: {
    display: 'flex',
    margin: '0 auto',
    marginTop: theme.spacing.unit * 1.5,        
    marginBottom: theme.spacing.unit * 2,
    justifyContent: 'center',      
  },
  button: {                
      marginRight: theme.spacing.unit,
      paddingLeft: theme.spacing.unit * 4,
      paddingRight: theme.spacing.unit * 4,
      paddingTop: theme.spacing.unit * 1.5,
      paddingBottom: theme.spacing.unit * 1.5,
      fontSize: 18
  },
  cta: {
      fontFamily:`Velvet, 'sans-serif'`,
      fontWeight: 600,
      textShadow: '2px 2px 2px #444',
      fontSize: 24,
      letterSpacing: 1.4,
      paddingLeft: theme.spacing.unit * 4,
      paddingRight: theme.spacing.unit * 4,
      paddingTop: 10,
      paddingBottom: 4,       
  },

});

function FormReview(props) {
  const { classes, values, carrito, montoCarrito, unidadesCarrito, isSubmitting, handleBack } = props;
  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Resumen del Pedido
      </Typography>
      {
          carrito &&
          <React.Fragment>
            <List disablePadding>        
              {
                carrito.map(item => (
                  <ListItem className={classes.listItem} key={item.id}>
                    
                      <img src={item.fotos[0].imgPath} alt={item.nombre} className={classes.imagen}/>
                    
                    <ListItemText
                      primary={item.nombre} 
                      secondary=
                        {
                          item.cantidad > 1 ? 
                          `${item.cantidad} unidades de $${item.precio} cada una` 
                        : 
                          `${item.cantidad} unidad de $${item.precio}`
                        }                
                    />
                    <Typography variant="body2">{`$${item.cantidad*item.precio}`}</Typography>
                  </ListItem>
                ))
              }
              
              {
                values.tipoEntrega === 'envio' &&
                <ListItem className={classes.listItem}>
                  <ListItemText 
                    style={{textAlign:'right'}}
                    primary='Costo de envo'                    
                  />
                    <Typography variant="body2">$100</Typography>
                </ListItem>
              }
              
              <ListItem className={classes.listItem}>
                <ListItemText 
                  primary={<b style={{fontSize: 20}}>Total</b>}
                  secondary={
                    (unidadesCarrito === 1) ? 
                      `${unidadesCarrito} unidad`
                    :
                      `${unidadesCarrito} unidades`
                  }
                />
                <Typography variant="subtitle1" className={classes.total} style={{fontSize: 20}}>
                  <b>
                    {
                      values.tipoEntrega === 'retiro' ?
                        `$${montoCarrito}`
                      :
                        `$${montoCarrito+100}`
                    }
                  </b>
                </Typography>
              </ListItem>
            </List>

            <Grid container spacing={16}>
              
              <Grid item xs={12} sm={6}>
                <Typography variant="h6" gutterBottom className={classes.title}>
                  Tus datos
                </Typography>
                <Typography variant='body1' gutterBottom>{values.name}</Typography>
                <Typography variant='body1' gutterBottom>
                  <MailIcon style={{marginBottom:-9, marginRight: 4}} />
                  {values.email}
                </Typography>
                <Typography variant='body1' paragraph>
                  <PhoneIcon style={{marginBottom:-9, marginRight: 4}} />
                  {values.tel}
                </Typography>
              </Grid>

              <Grid item container direction="column" xs={12} sm={6}>
                <Typography variant="h6" gutterBottom className={classes.title}>
                  Forma de entrega
                </Typography>
                
                <Grid container>
                    <Grid item xs={12}>
                      <Typography  variant='body1' gutterBottom>
                        <b>
                          {
                            values.tipoEntrega === 'retiro' ?
                              'Retiro por el barrio de Colegiales'
                            :
                              'Envo a domicilio'
                          }
                        </b>
                      </Typography>
                    </Grid>                    
                    {
                      values.tipoEntrega === 'envio' &&
                        <React.Fragment>
                          <Grid item xs={6}>
                            <Typography gutterBottom>Da de entrega</Typography>
                          </Grid>
                          <Grid item xs={6}>
                            <Typography variant='subtitle2' gutterBottom>{values.entregaDia}</Typography>
                          </Grid>
                          <Grid item xs={6}>
                            <Typography gutterBottom>Direccin de entrega</Typography>
                          </Grid>
                          <Grid item xs={6}>
                            <Typography variant='subtitle2' gutterBottom>{values.entregaDireccion}</Typography>
                          </Grid>
                        </React.Fragment>
                    }                                                                  
                </Grid>
              </Grid>

              { isSubmitting && <LinearProgress /> }

              <div className={classes.buttons}>   
                  <Tooltip title='Volver a Forma de entrega'>                                                                                     
                    <Button size='small' onClick={() => handleBack()} className={classes.button} style={{fontSize: 12}}>
                        Atrs
                    </Button>                                                
                  </Tooltip>
                  <Tooltip title='Realizar el pedido'>
                    <Button                                                
                        size='large'
                        type="submit"
                        disabled={isSubmitting}
                        variant="contained"
                        color="primary"                                                
                        className={classes.cta}
                    >
                        Confirmar pedido
                    </Button>
                  </Tooltip>
              </div> 

            </Grid>
          </React.Fragment>
      }

      { !carrito &&
        <div style={{height: '650px'}}>
          <LinearProgress className={classes.progress} />
        </div>
      }

      
    </React.Fragment>
  );
}

FormReview.propTypes = {
  classes: PropTypes.object.isRequired,
  values: PropTypes.object.isRequired,
  carrito: PropTypes.array,
  montoCarrito: PropTypes.number,
  unidadesCarrito: PropTypes.number,
  fechaEntrega: PropTypes.string,
  isSubmitting: PropTypes.bool.isRequired,
  handleBack: PropTypes.func.isRequired,
};

export default withStyles(styles)(FormReview);