import React from 'react';
import PropTypes from 'prop-types';

import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

import FormDatos from './FormDatos';
import FormEntrega from './FormEntrega';
import FormReview from './FormReview';
import CarritoVacio from '../Carrito/CarritoVacio';
import Menu from '../Home/Menu';
import { getMontoCarrito, getUnidadesCarrito } from '../../helpers/carritoHelpers';

import { calcularDiaEntrega } from '../../helpers/checkoutHelpers';

import Fade from 'react-reveal/Fade';

import withStyles from '@material-ui/core/styles/withStyles';
const styles = theme => ({    
    grid: {
        marginRight: theme.spacing.unit * 2, 
        marginLeft: theme.spacing.unit * 2
    },
    paper: {        
        margin: '0 auto', 
        marginTop: theme.spacing.unit * 4,
        marginBottom: theme.spacing.unit * 6,
        padding: `${theme.spacing.unit * 2.5}px ${theme.spacing.unit * 4}px`,        
        [theme.breakpoints.up(600 + theme.spacing.unit * 3 * 2)]: {
            width: 600,                    
            padding: `${theme.spacing.unit * 3}px ${theme.spacing.unit * 8}px`,
        },
    },
    header: {
        fontFamily: `'Velvet', sans-serif`,
        fontWeight: 600,        
    },  
    stepper: {
        padding: `${theme.spacing.unit * 3}px 0 ${theme.spacing.unit * 5}px`,
    },
    buttons: {
        display: 'flex',
        margin: '0 auto',
        marginTop: theme.spacing.unit * 2,        
        marginBottom: theme.spacing.unit * 2,
        justifyContent: 'center',      
    },
    button: {                
        paddingLeft: theme.spacing.unit * 4,
        paddingRight: theme.spacing.unit * 4,
        paddingTop: theme.spacing.unit * 1.5,
        paddingBottom: theme.spacing.unit * 1.5,
        fontSize: 18
    },
});
  

const steps = ['Tus datos', 'Forma de entrega', 'Revis tu pedido'];

const Checkout = ({ classes, activeStep, validationStep, carrito, 
    handleNext, handleBack, handleNextValidationStep,
    direcciones, handleRemoverDireccion, handleHomeClick,
    values, errors, isValid, setFieldTouched, setFieldValue, isSubmitting
}) => {
    return (
        <React.Fragment>            

            <Menu                            
                unidades={getUnidadesCarrito(carrito)}
                fixed={false}
                homeSeleccionable={true}
                exitConfirmation={true}
                carritoVisible={false}
                carritoSeleccionable={false}                
                handleHomeClick={handleHomeClick}
            />                         
              
            <Grid container justify="center" alignItems="center">
                <Grid item xs={12} className={classes.grid}>
                    <Fade>
                        <Paper className={classes.paper}>
                                                    
                            <Typography component='h2' variant="h4" align="center" className={classes.header}>  
                                Finalizar compra
                            </Typography>
                            
                            {/* CARRITO VACO, NO MOSTAR CHECKOUT */}
                            { 
                                (carrito.length === 0) &&
                                <CarritoVacio 
                                    handleHomeClick={handleHomeClick}
                                />
                            }
                            
                            { 
                                (carrito.length > 0) &&
                                <React.Fragment>
                                                                    
                                    <Stepper activeStep={activeStep} className={classes.stepper} > 
                                        {
                                            steps.map(label => (
                                                <Step key={label}>
                                                    <StepLabel>{label}</StepLabel>
                                                </Step>
                                            ))
                                        }
                                    </Stepper>

                                                               
                                    
                                    {/* PASO 'TUS DATOS' */}
                                    {
                                        activeStep === 0 && 
                                            <FormDatos
                                                validationStep={validationStep}
                                                values={values}
                                                errors={errors}
                                                isValid={isValid}
                                                handleNext={handleNext}
                                                handleNextValidationStep={handleNextValidationStep}
                                                setFieldTouched={setFieldTouched}
                                            />
                                    }
                                    
                                    {/* PASO FORMA DE ENTREGA */}
                                    {
                                        activeStep === 1 && (
                                            <FormEntrega
                                                dia1={calcularDiaEntrega(1)}
                                                dia2={calcularDiaEntrega(2)}
                                                direcciones={direcciones}
                                                validationStep={validationStep}
                                                values={values}
                                                errors={errors}
                                                handleNext={handleNext}
                                                handleNextValidationStep={handleNextValidationStep}
                                                setFieldTouched={setFieldTouched}
                                                setFieldValue={setFieldValue}
                                                handleBack={handleBack}
                                                handleRemoverDireccion={handleRemoverDireccion}
                                            />                                        
                                        )
                                    }
                                    {
                                        activeStep === 2 && (
                                            <React.Fragment>
                                                <FormReview 
                                                    values={values} 
                                                    carrito={carrito} 
                                                    montoCarrito={getMontoCarrito(carrito)}
                                                    unidadesCarrito={getUnidadesCarrito(carrito)}                                                     
                                                    isSubmitting={isSubmitting}
                                                    handleBack={handleBack}
                                                />                                                                                                                      
                                            </React.Fragment>
                                        )
                                    }                            
                                           
                                </React.Fragment>
                            }            
                        </Paper>
                    </Fade>
                </Grid>
            </Grid>
            
        </React.Fragment>
    );
};

Checkout.propTypes = {
    activeStep: PropTypes.number.isRequired,
    validationStep: PropTypes.number.isRequired,
    carrito: PropTypes.array.isRequired,
    handleNext: PropTypes.func.isRequired,
    handleBack: PropTypes.func.isRequired,
    handleNextValidationStep: PropTypes.func.isRequired,           
    direcciones: PropTypes.array.isRequired,    
    handleRemoverDireccion: PropTypes.func.isRequired,
    handleHomeClick: PropTypes.func.isRequired,
    values: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired,
    isValid: PropTypes.bool.isRequired,
    setFieldTouched: PropTypes.func.isRequired,
    setFieldValue: PropTypes.func.isRequired, 
    isSubmitting: PropTypes.bool.isRequired, 
};

export default withStyles(styles)(Checkout);