import React from 'react';
import PropTypes from 'prop-types';
import Snackbar from '@material-ui/core/Snackbar';
import Button from '@material-ui/core/Button';
import { Link } from "react-router-dom";
import HeadShake from 'react-reveal/HeadShake';


import { withStyles } from '@material-ui/core/styles';
import { Tooltip } from '@material-ui/core';
const styles = (theme) => ({            
    cta: {
        fontSize:20,
        letterSpacing: '1.4px', 
        fontWeight: 600,
        fontFamily: `Velvet, 'sans-serif'`,
        textShadow: '1px 1px 1px #444',
        margin: 0, 
        padding: 0, 
        color: '#fff',
    }
});

const ProductoAgregado = ({ classes, unidades, monto, mensaje, show, onClose }) => {
    return (
        <Snackbar                            
            open={show}            
            onClose={onClose}     
            autoHideDuration={8000}       
            ContentProps={{
                'aria-describedby': 'aria-label',
            }}
            message={                
                <span style={{color:'#d4f14f',fontSize:22}}>Total: {unidades} huevos (${monto})</span>
            }
            action={                                    
                unidades > 0 &&
                    <HeadShake delay={500}>
                        <Tooltip title='Clic aqu para avanzar con la compra de los productos elegidos'>
                            <Link to={`/carrito`} style={{textDecoration: 'none'}}>
                                <Button size="small" className={classes.cta}>
                                    {`>> Terminar la compra <<`}
                                </Button>                                        
                            </Link>
                        </Tooltip>
                    </HeadShake>                
            }
        />
    );
};


ProductoAgregado.propTypes = {  
    classes: PropTypes.object.isRequired,
    unidades: PropTypes.number.isRequired,
    monto: PropTypes.number.isRequired,
    mensaje: PropTypes.string.isRequired,
    show: PropTypes.bool.isRequired,    

    onClose: PropTypes.func.isRequired,
};

export default withStyles(styles)(ProductoAgregado);