import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import { Link } from "react-router-dom";

import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({
   titulo: {        
        fontSize: 14,
        marginTop: theme.spacing.unit * 6
    },
    container: {
        width: '100%',
        textAlign:'center',
        marginBottom: theme.spacing.unit * 3
    },
    button: {
        paddingTop: theme.spacing.unit * 1.5,
        paddingBottom: theme.spacing.unit * 1.5,
        paddingLeft: theme.spacing.unit * 3,
        paddingRight: theme.spacing.unit * 3        
    }
});

const CarritoVacio = ({ classes }) => {
    return (
        <React.Fragment>
            <Typography variant="caption" align="center" className={classes.titulo} paragraph>
                Tu carrito de compras est vaco
            </Typography>
            <div className={classes.container}>
                <Link to='/' style={{textDecoration: 'none'}}>
                    <Tooltip title='Volver a la pgina principal'>
                        <Button variant="contained" color='primary' className={classes.button}>
                            Continuar comprando aqu
                        </Button>
                    </Tooltip>
                </Link>
            </div>
        </React.Fragment>
    );
};

CarritoVacio.propTypes = {
    classes: PropTypes.object.isRequired,    
};
export default withStyles(styles)(CarritoVacio);