import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Link } from "react-router-dom";

import { withStyles } from '@material-ui/core/styles';
import { Tooltip } from '@material-ui/core';
const styles = (theme) => ({  
    root: {
        textAlign: 'center',
        paddingLeft: theme.spacing.unit * 6,
        paddingRight: theme.spacing.unit * 6,
    },
    subtotal: {
        marginTop: 30,
        marginBottom: 10,
    },
    cta: {
        fontFamily:`Velvet, 'sans-serif'`,
        fontWeight: 600,
        textShadow: '2px 2px 2px #444',
        fontSize: 24,
        letterSpacing: 1.4,
        paddingLeft: theme.spacing.unit * 4,
        paddingRight: theme.spacing.unit * 4,
        paddingTop: 10,
        paddingBottom: 4,
        marginTop: 10,
        marginBottom: 50,        
    }
});

const CarritoResumen = ({ classes, subtotal }) => {
    return (        
        <div className={classes.root}>
            <Typography variant="h5" align="center" className={classes.subtotal}>                  
                Total: <b>{`$${subtotal}`}</b>
            </Typography>
            <Typography variant="caption" align="center" >                  
                Podrs ingresar los datos de envo o elegir la opcin de retiro en el siguiente paso
            </Typography>

            <Link to='/checkout' style={{textDecoration: 'none'}}>
                <Tooltip title='Avanzar con la compra de estos productos'>
                    <Button size='large' color="primary" variant='contained' className={classes.cta}>
                        Siguiente paso                                               
                    </Button>
                </Tooltip>
            </Link>
        </div>
    );
};

CarritoResumen.propTypes = {
    classes: PropTypes.object.isRequired,
    subtotal: PropTypes.number.isRequired,    
};
export default withStyles(styles)(CarritoResumen);