import React from 'react';
import PropTypes from 'prop-types';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import InputAdornment from '@material-ui/core/InputAdornment';
import Grid from '@material-ui/core/Grid';

import { Field } from 'formik';
import { TextField } from 'formik-material-ui';

import { isRequired, validarPaso1, validateTel, validateEmail } from '../../helpers/checkoutHelpers';

import withStyles from '@material-ui/core/styles/withStyles';
import { Tooltip } from '@material-ui/core';
const styles = theme => ({    
    buttons: {
        display: 'flex',
        margin: '0 auto',
        marginTop: theme.spacing.unit * 2,        
        marginBottom: theme.spacing.unit * 2,
        justifyContent: 'center',      
    },
    
    cta: {
        fontFamily:`Velvet, 'sans-serif'`,
        fontWeight: 600,
        textShadow: '2px 2px 2px #444',
        fontSize: 24,
        letterSpacing: 1.4,
        paddingLeft: theme.spacing.unit * 4,
        paddingRight: theme.spacing.unit * 4,
        paddingTop: 10,
        paddingBottom: 4,       
    },

    titleMargin: {
        marginTop: theme.spacing.unit * 2, 
        marginBottom: theme.spacing.unit * 2
    },

});

const FormDatos = ({ classes, validationStep, values, errors, isValid, handleNext, handleNextValidationStep, setFieldTouched }) => {
    return (
        <Grid container spacing={24}>

            <Typography variant="subtitle2" align='center' style={{fontSize: 20}} className={classes.titleMargin}>
                Ingres tus datos para comunicarnos con vos y coordinar la entrega
            </Typography>

            {/* CAMPO NOMBRE */}
            <Grid item xs={12}>                                                                      
                <Field                                                                 
                    onKeyPress={ 
                        (e) => {
                            if (e.key === 'Enter') {                                                                            
                                validarPaso1(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                            }
                        }
                    }
                    variant='outlined' fullWidth type="text" component={TextField} name="name" 
                    validate={isRequired} label="Tu nombre"
                />                                                                                                                                                    
            </Grid>

            {/* CAMPO CELULAR */}
            <Grid item xs={12}>                                                                
                <Field 
                    onKeyPress={ 
                        (e) => {
                            if (e.key === 'Enter') {                                                                            
                                validarPaso1(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                            }
                        }
                    }                                                                
                    variant='outlined' fullWidth type="tel" minLength='5' maxLength='12' component={TextField} 
                    name="tel" validate={validateTel} 
                    label="Nmero de celular" placeholder="Nmero de celular"
                    InputProps={{
                        startAdornment: <InputAdornment position="start">(15) </InputAdornment>,
                    }}
                />                                                                
            </Grid>

            {/* CAMPO CORREO ELECTRNICO */}
            <Grid item xs={12}>                                                                
                <Field                                                                 
                    onKeyPress={ 
                        (e) => {
                            if (e.key === 'Enter') {                                                                            
                                validarPaso1(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                            }
                        }
                    }
                    variant='outlined' fullWidth type="email" component={TextField} name="email" 
                    validate={validateEmail} label="Correo electrnico"                                                                
                />                                                                                                            
            </Grid>
                                                                    
            <div className={classes.buttons}>
                <Tooltip title='Continuar con la forma de entrega'>
                    <Button
                        variant="contained"
                        size='large'
                        color="primary"
                        onClick={() => validarPaso1(values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)}                                            
                        className={classes.cta}
                    >
                        Continuar
                    </Button>
                </Tooltip>
            </div>

        </Grid>
    );
};

FormDatos.propTypes = {
    classes: PropTypes.object.isRequired,
    validationStep: PropTypes.number.isRequired, 
    values: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired,
    isValid: PropTypes.bool.isRequired,
    handleNext: PropTypes.func.isRequired, 
    handleNextValidationStep: PropTypes.func.isRequired, 
    setFieldTouched: PropTypes.func.isRequired, 
};

export default withStyles(styles)(FormDatos);