export const calcularDiaEntrega = diaEntrega => {
    
    let tomorrow = new Date();
    
        
    tomorrow.setDate(tomorrow.getDate() + 1);    
    let diaSemana = tomorrow.getDay();
    let diaNumero = tomorrow.getDate();

    while ( (diaSemana !== 2) && (diaSemana !== 6) ) {
        tomorrow.setDate(tomorrow.getDate() + 1);    
        diaSemana = tomorrow.getDay();
        diaNumero = tomorrow.getDate();
    }

    if (diaEntrega === 1) {
        if (diaSemana === 2) {
            return `Martes ${diaNumero} de 10 a 14hs`
        };    
        if (diaSemana === 6) {
            return `Sbado ${diaNumero} de 10 a 14hs`
        };
    }
    else {
        tomorrow.setDate(tomorrow.getDate() + 1);    
        diaSemana = tomorrow.getDay();
        diaNumero = tomorrow.getDate();
        while ( (diaSemana !== 2) && (diaSemana !== 6) ) {
            tomorrow.setDate(tomorrow.getDate() + 1);    
            diaSemana = tomorrow.getDay();
            diaNumero = tomorrow.getDate();
        }   
    }
    
    if (diaSemana === 2) {
        return `Martes ${diaNumero} de 10 a 14hs`
    };    
    if (diaSemana === 6) {
        return `Sbado ${diaNumero} de 10 a 14hs`
    };           
}

export const isRequired = value => {
    let error;
    if (!value) {
        error = 'Campo requerido';
    } 
    return error;
};

export const validateEmail = value => {
    let error;
    if (!value) {
        error = 'Campo requerido';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
        error = 'Direccin de email invlida';
    }
    return error;
};

export const validateTel = value => {
    let error;
    if (!value) {
        error = 'Campo requerido';
    } else if (!/^\d*$/.test(value)) {
        error = 'Nmero de telfono incorrecto';
    } else if (value < 10000 || value > 999999999999) {
        error = 'Nmero de telfono incorrecto';
    }
    return error;
};

export const validarPaso1 = (values, isValid, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched) => {            
    if (validationStep === 0) handleNextValidationStep();    
    
    setFieldTouched('name', true);
    setFieldTouched('email', true);
    setFieldTouched('tel', true);

    if ( isValid || (values.name !== '' && values.email !== '' && values.tel !== '' && !errors.name && !errors.email && !errors.tel) ) {
        handleNext();
        window.scrollTo({top: 0,left: 0, behavior: 'smooth'});
    }
}

export const validarPaso2 = (values, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched) => {            
    if (validationStep === 1) handleNextValidationStep();
    
    setFieldTouched('tipoEntrega', true);
    setFieldTouched('entregaDia', true);    
    setFieldTouched('entregaDireccion', true);
        
    if ( 
            (values.tipoEntrega === 'retiro') || 
            (values.tipoEntrega === 'envio' && values.entregaDia !== '' && values.entregaDireccion !== '' && !errors.entregaDia && !errors.entregaDireccion) 
        ) {
        handleNext();
        window.scrollTo({top: 0,left: 0, behavior: 'smooth'});
    }
}
