import React from 'react';
import PropTypes from 'prop-types';

import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import Fade from 'react-reveal/Fade';
import { Formik, Field, Form } from 'formik';
import { TextField } from 'formik-material-ui';

import logo from '../../images/logo.svg';
import instagram from '../../images/instagram.svg';
import fondo from '../../images/fondo2.png';

import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({        
    root: {
        backgroundImage: `url(${fondo})`,        
        height: '100%',
        minHeight: 350,
        padding: theme.spacing.unit * 4,
        paddingBottom: theme.spacing.unit
    },
    logo: {    
        width: 72,                
    },
    red: {
        height: 20,
        marginRight: theme.spacing.unit * 0.5,                
    },
    tituloNews: {
        fontFamily: `'Velvet', sans-serif`,
        fontSize: 18,
        fontWeight: 600,
        textShadow: '1px 1px 1px #444',
        color: '#fff',
        paddingLeft: theme.spacing.unit * 4,
        lineHeight: 1.8, 
        [theme.breakpoints.up('sm')]: {
            fontSize: 20,
        },      
    },
    gridGracias: {
        paddingLeft: theme.spacing.unit * 4,
    },
    textoGracias: {
        fontFamily: `'Velvet', sans-serif`,
        fontSize: 16,
        fontWeight: 600,
        textShadow: '1px 1px 1px #444',
        color: '#fff',
        [theme.breakpoints.up('sm')]: {
            fontSize: 18,
        }, 
    },
    submit: {        
        
        color: '#fff',
    },
    fullHeight: {
        height:'100%',
    }
});

const Newsletter = ({ classes, newsletter, handleNuevoNewsletter }) => {
    return (
        <Fade duration={1000}>
            <div className={classes.root}>              
                <Grid container direction='row' justify='flex-start' className={classes.fullHeight}>
                                    
                    <Grid container item xs={12}>
                        <Grid item>
                            <img src={logo} alt={"Velvet Chocolates"} className={classes.logo}/>
                        </Grid>                        

                        {
                            newsletter === '' ?
                                <Grid item xs>                                    
                                    <Typography variant="overline" align="left" color="textPrimary" component="p" className={classes.tituloNews}>
                                        Suscribite a nuestro newsletter y enterate de todas las novedades                                            
                                    </Typography>
                                </Grid>
                            :
                                <Grid item xs className={classes.gridGracias}>
                                    <Typography variant="subtitle1" align="left" component="p" className={classes.textoGracias} paragraph>
                                        <b>Gracias</b> por suscribirte, <span style={{fontFamily: 'sans-serif', fontWeight: 400}}>{newsletter}</span>.                                        
                                    </Typography>
                                    <Typography variant="subtitle1" align="left" className={classes.textoGracias} style={{fontSize:14}}>
                                        Te informaremos de nuestras novedades y nuevos productos.
                                    </Typography>
                                </Grid>
                        }
                    </Grid>
                    
                    {
                        newsletter === '' &&
                            <Grid container item xs={12}>
                                <Formik
                                    initialValues={{ formNewsletter: ''}}
                                    validate={values => {
                                        let errors = {};
                                        if (!values.formNewsletter) {
                                        errors.formNewsletter = 'Ingres una direccin de correo electrnico para suscribirte';
                                        } else if (
                                        !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.formNewsletter)
                                        ) {
                                        errors.formNewsletter = 'Direccin de correo electrnica incorrecta';
                                        }
                                        return errors;
                                    }}
                                    onSubmit={(values, { setSubmitting }) => {
                                        handleNuevoNewsletter(values.formNewsletter);
                                        setSubmitting(false);                                                
                                    }}
                                >
                                    {({                                                
                                        handleSubmit,
                                        isSubmitting,                                            
                                    }) => (
                                        <Form style={{width: '100%'}}> 
                                            <Grid container direction='row' item xs={12} alignItems='center'>
                                                <Grid item xs>
                                                    <Tooltip title='Ingresa tu direccin y presion OK para suscribirte'>
                                                        <Field 
                                                            onKeyPress={ (e) => { if (e.key === 'Enter') {handleSubmit()} }}
                                                            fullWidth type="email" component={TextField} name="formNewsletter" 
                                                            label="Tu direccin de email" 
                                                        />
                                                    </Tooltip>
                                                </Grid>
                                                <Grid item >
                                                    <Button type='submit' disabled={isSubmitting}                                                        
                                                        size='small' fullWidth className={classes.submit}
                                                    >
                                                        OK
                                                    </Button>
                                                </Grid>
                                            </Grid>
                                        </Form>
                                    )}
                                </Formik>                                    
                            </Grid>
                    }
                
                    <Grid container direction='row' item xs={12} style={{alignSelf: 'flex-end'}}>                                    
                        <Grid item xs={1}>
                            <a 
                                href="https://www.instagram.com/velvet.chocolates" target='_blank' rel="noopener noreferrer" 
                                style={{textDecoration:'none'}}
                            >
                                <Tooltip title='Ir a Instagram'>
                                    <img src={instagram} alt={"Velvet Chocolates"} className={classes.red}/>                            
                                </Tooltip>
                            </a>
                        </Grid>
                                                
                        <Grid item xs={5}>
                            <a 
                                href="https://www.instagram.com/velvet.chocolates" target='_blank' rel="noopener noreferrer" 
                                style={{textDecoration:'none'}}
                            >
                                <Tooltip title='Ir a Instagram'>
                                    <Typography 
                                        variant="caption" align="left" color="textPrimary" component="p" 
                                        style={{color: '#fff', textShadow: '1px 1px 1px #444'}}
                                    >
                                        @velvet.chocolates
                                    </Typography>      
                                </Tooltip>   
                            </a>
                        </Grid> 
                                      
                        <Grid item xs={6}>
                            <a href="https://github.com/agusmafud" target='_blank' rel="noopener noreferrer" style={{textDecoration:'none'}}>
                                <Tooltip title='Ir a GitHub'>
                                    <Typography 
                                        variant="caption" align="right" color="textPrimary" component="p" 
                                        style={{color: '#fff', textShadow: '1px 1px 1px #444'}}
                                    >
                                        Desarrollado por @agusmafud
                                    </Typography>          
                                </Tooltip>        
                            </a>
                        </Grid>                                                                                              
                    </Grid>

                  

                </Grid>
            </div>
        </Fade>
    );
};

Newsletter.propTypes = {
    classes: PropTypes.object.isRequired,
    newsletter: PropTypes.string.isRequired,
    handleNuevoNewsletter: PropTypes.func.isRequired,
};

export default withStyles(styles)(Newsletter);